export class NewsData {
    init(){
        id = 0
        link = ""
        category = ""
        source = ""
        title = ""
        content = ""
        detail = ""
        isActive = false
        isOption = false
    }


    constructor(id, link, category, source, title, content, detail) {
        this.id = id;
        this.link = link
        this.category = category;
        this.source = source;
        this.title = title;
        this.content = content;
        this.detail = detail;
        this.isOption = false
    }
}