<script>
    export let listFav;
    export let listDislike;
    export let isShowPopup;
    export let listTagFav;
    import { getContext } from 'svelte';
    import favPopup from "./favPopup.svelte";
    const { open, close } = getContext('simple-modal');
    if(isShowPopup){
      goToFav()
    }
    function goToFav(){
      console.log("hey")
      open(favPopup, {tagfav: listTagFav, listFav: listFav, listDislike: listDislike }, {}, { onClosed: () => {  }})
    }
    let isHovered = false;
    function handleMouseHover() { isHovered = true; }
    function handleMouseOut() { isHovered = false; }
</script>
<div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};" on:click={goToFav}>Favorites</div>