<script>
    import { Interactor, NewsData } from "./lib/index.js";
	import { onDestroy } from "svelte";
	import Modal from 'svelte-simple-modal';
	import FavTab from "./module/FavTab.svelte";
    import { inview } from 'svelte-inview';
    const interactor = new Interactor()
	
    var listNews = []
    let pageNews = 1
    let Account = "Account"
    let isOptionShow = true
    let listLoad = {}
    let refreshList = {}
    var scrollListener = {}
    var isLoadNow = true
    var isLoadPref = false
    var localPref = {fav: [], dislikes: [], tagfav: []}
    var isShowPopup = false
    var modeList = "unreaded"
    var localHeight = {}
    const options = {};
    function fetchNewListData(){
        isLoadNow = true
        interactor.fetchListNews(pageNews, modeList, sessionLogin).then(newsDataList => {
			listNews = listNews.concat(newsDataList)
            if(newsDataList.length>0){
                isLoadNow = false
            }
        })
    }
    function onClickOption(item){
        return () => {
            for(let i=0;i<listNews.length;i++){
                if(listNews[i].id == item.id){
                    listNews[i].isOption=!item.isOption
                }else{
                    listNews[i].isOption = false
                }
            }
        }
    }
    function onPressBlock(item){
        return () => {
            console.log("gheafughai")
            onClickOption(item)()
        }
    }
    function onPressFav(item){
        return () => {
            console.log("398759347659")
            onClickOption(item)()
        }
    }
    function getImageEror(event){
    }
    import { listPref } from './lib/sharedVar.js';
    import FavPopup from "./module/favPopup.svelte";
    let markAsReadLink = (link) => {
        return "https://passagenews.000webhostapp.com/passagenews.php?newsreaded="+link
    }
	const unsubscribe = listPref.subscribe((value) => {
        if(isLoadPref){
            console.log(value)
            interactor.savePrefUser(btoa(JSON.stringify(value)), sessionLogin).then(() => {
                pageNews = 1
                listNews = []
                fetchNewListData()
            })
        }
	}); // logs 'got a subscriber', then '1'
	function createImgSizer(url){
        return (event) => {
            event.target.style = ""
            let widthImg =  event.target.width
            let heightImg = event.target.height
            let heigthImage = (heightImg/widthImg)*(window.frames.innerWidth*0.78);
            let widthImage = (widthImg/heightImg)*heigthImage;
            event.target.width = widthImage;
            event.target.height = heigthImage;
            
        }
    }
    function getImageSize(event) {
        // let indexTemp = listNews.indexOf(event.target.src)
        // listNews[indexTemp].isActive = true
        event.target.style = ""
        let widthImg =  event.target.width
        let heightImg = event.target.height
        let heigthImage = (heightImg/widthImg)*(window.frames.innerWidth*0.78);
        let widthImage = (widthImg/heightImg)*heigthImage;
        localHeight[event.target.src] = {height: heigthImage, width: widthImage}
        event.target.width = widthImage;
        event.target.height = heigthImage;
    }
    function getColorTabBtn(modeCheck){
        if (modeList==modeCheck) {
            return "background-color: #006944;"
        }
    }
    let isAtBottom = false;

    function titleClick(){
        
    }
    function checkIfAtBottom() {
        const scrollY = window.scrollY;
        const windowHeight = window.innerHeight;
        const documentHeight = document.documentElement.scrollHeight;
        isAtBottom = scrollY + windowHeight  >= (documentHeight-20);
        scrollListener = {}
        if(isAtBottom){
            if(isLoadNow==false){
                pageNews+=1
                fetchNewListData()
            }
        }
    }
	function onPressGetList(modeCheck){
        return ()=>{
            pageNews = 1
            listNews = []
            switch(modeCheck){
                case "readed": modeList = "readed";break
                case "unreaded": modeList = "unreaded";break
                case "fav": modeList = "favorite";break
                default: modeList = "";break
            }
            scrollListener = {}
            fetchNewListData()
        }
    }
    function onPressNext(){
        if(isLoadNow==false){
            pageNews+=1
            fetchNewListData()
        }
    }
	function getSession(){
		const searchParams = new URLSearchParams(window.location.search);
		// Get a specific parameter
		const token = searchParams.get('token');
		const name = searchParams.get('name');
        console.log("test name : "+name)
        Account = name
        if ((token == null) || (token == "") || (token == undefined) && 
            (name == null) || (name == "") || (name == undefined)){
			// document.cookie = "sessionLogin=";
        }else{
            // console.log("sessionLogin="+token)
			document.cookie = "sessionLogin="+token;
            document.cookie = "name="+name;
			// window.location.href = window.location.origin+window.location.pathname;
		}
	}
    const getLoginSession = () => {
		const cookieName = 'sessionLogin';
        const username = 'name';
    	const cookies = document.cookie.split('; ');
        console.log(cookies)
        let nameValue = ""
		let cookieValue = ""
		for (const cookie of cookies) {
            console.log("test cookie 2 : "+cookie)
			const [name, value] = cookie.split('=');
            if (name === cookieName) {
				cookieValue = value;
			}else if (name == username){
                nameValue = value;
                Account = value;
            }
		}
		return [cookieValue, nameValue]
	}
    getSession()
    const [sessionLogin, userName] = getLoginSession()
    fetchNewListData()
    interactor.getPrefUser(sessionLogin).then( data => {
        if(data["fav"].length==0 && data["dislikes"].length==0 && data["tagfav"].length==0){
            console.log("hello")
            localPref = {fav: [""], dislikes: [""], tagfav: [""]}
            isShowPopup = true
            //Show popup
        }else{
            console.log("its real")
            localPref = data
        }
        isLoadPref = true
    })
    let isWideScreen = window.frames.innerWidth>window.frames.innerHeight
    function windowStyle(){
        let widthCheck = isWideScreen ? "15% 85%" : "100%"
        return "display: grid;grid-template-columns:"+widthCheck
    }
    function TabStyleCheck(){
        let widthCheck = isWideScreen==false ? "100px 100px 100px 100px 100px" : "30px 30px 30px 30px 30px"
        let checkRow = isWideScreen==false ? "columns" : "row"
        return "display: grid;grid-template-"+checkRow+":"+widthCheck
    }
    function testVis(test){
        console.log("hello boy")
        console.log(test)
    }
    let isHovered = false;
    function handleMouseHover() { isHovered = true; }
    function handleMouseOut() { isHovered = false; }

    
</script>
<style>
    .container {
		display: flex;
	  	justify-content: flex-end ;
	  	align-items: center;
	}
    .hovering-div {
        position: fixed;
        top: 10px;
        left: 10px;
  }
</style>
<svelte:window on:scroll={checkIfAtBottom} />
<div style={windowStyle()}>
<div>
    {#key scrollListener}
        <div class="hovering-div" style={"background-color: #0dad77;padding-left: 10px;padding-right: 10px"}>
            <div style={TabStyleCheck()}>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px" on:click={onPressGetList("all")}>Logo</div>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px" on:click={onPressGetList("all")}>All</div>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("favorite")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px" on:click={onPressGetList("fav")}>favorite</div>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("unreaded")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px" on:click={onPressGetList("unreaded")}>unclicked</div>
                <div on:mouseover={handleMouseHover} on:mouseout={handleMouseOut} style="{getColorTabBtn("readed")};cursor: {isHovered ? 'pointer' : 'auto'};padding: 5px" on:click={onPressGetList("readed")}>clicked</div>
            </div>
        </div>
    {/key}
</div>
<div>
    {#if isWideScreen==false}
        <div style="height: 50px"></div>
    {/if}
    <div style="height:80px;width:100%" class="container">
		<Modal styleWindow={{ width: "50%", height: "100%"}}>
			{#key isShowPopup}
                <FavTab listTagFav={localPref.tagfav.concat("")} isShowPopup={isShowPopup} listFav={localPref.fav.concat("")} listDislike={localPref.dislikes.concat("")}></FavTab>
            {/key}
		</Modal>
		<div style="margin:10px"><img id="imagepassage" src="https://github.com/oreki9/passage/blob/main/user.png?raw=true" style="width: 50px;height: 50px;border-radius: 50%;"></div><a href="https://passagenews.000webhostapp.com/login.php?login=true">{Account}</a>
	</div>
    <div style="flex-direction: column;background-color: #FFFFFF;">
    {#key listNews}
        {#each listNews as item}
            <div style="background-color: #0dad77;margin: 20px;padding: 10px; border-radius: 10px">
                {#if item.isOption}
                <div style="position: {item.isOption ? "absolute" : "none"}; z-index: 1;right: 0;width: 300px;right: 20px;">
                    <div style="justify-content: flex-end; width: 100%; margin-top: 10px; margin-right: 10px; background-color: #15e65a">
                      <div on:click={onPressBlock(item)}>
                        <div style="align-self: flex-end; paddingLeft: 5px; height: 25px">Block</div>
                      </div>
                      <div on:click={onPressFav(item)}>
                        <div style="paddingLeft: 5px; height: 25px">favorite</div>
                      </div>
                    </div>
                  </div>
                {/if}
                <div style="display: grid;grid-template-columns: 97% 3%;">
                    <a style="text-decoration: none;" href={markAsReadLink(item.link)}>
                        <div style="padding-top: 10px">{item.title ?? ""}</div>
                    </a>
                    <div on:click={onClickOption(item)} style="background-color: #0daddd;padding: 10px;display: flex;justify-content: center;border-radius: 10px">::</div>
                </div>
                <div style="padding-top: 10px; justify-content: center;display: flex;">
                    <img use:inview={options}
                    on:inview_change={(event) => {
                      const { inView, entry, scrollDirection, observer, node} = event.detail;
                      let heightTemp = event.target.height
                      let widthTemp = event.target.width
                      if(inView){
                        if (localHeight[item.content] != undefined){
                            heightTemp = localHeight[item.content].height
                            widthTemp = localHeight[item.content].width
                        }
                        event.target.src = item.content
                    }else{
                        event.target.src = "https://wallpapercave.com/wp/wp4371959.jpg"
                    }
                    event.target.height = heightTemp
                    event.target.width = widthTemp
                    }} style="display: none" src={item.content ?? ""} on:load={getImageSize} on:error={getImageEror}/>
                </div>
                <div style="padding-top: 10px">{item.detail ?? ""}</div>
                <div style="padding-top: 10px">{item.source ?? ""}</div>
            </div>
        {/each}
    {/key}
    </div>
    <div style="justify-content: center;display: flex;height: 25px" on:click={onPressNext}>
        Press to Load More
    </div>
</div>
</div>